import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, XSquare, CheckSquare, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import { Link, useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../ClientSideDataTable'

const PendingServiceLogicList = () => {
    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [pendingServiceList, setpendingServiceList] = useState([])

    useEffect(() => {
        localStorage.setItem('useBMStoken', true)
        useJwt.getPendingServiceLogicList().then(res => {
            console.log(res)
            setpendingServiceList(res.data)
            localStorage.setItem('useBMStoken', false)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err)
            setTableDataLoading(false)
            localStorage.setItem('useBMStoken', false)
        })
    }, [resetData])
    const handlePoPupActions = (id, status, message) => {
        localStorage.setItem('useBMStoken', true)
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                return useJwt.approveOrRejectServiceLogic(id, status).then(res => {
                    Success(res)
                    console.log(res)
                    setpendingServiceList(pendingServiceList.filter(x => x.id !== id))
                    localStorage.setItem('useBMStoken', false)
                }).catch(err => {
                    localStorage.setItem('useBMStoken', false)
                    console.log(err.response)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {

            }
        })

    }
    const column = [
        {
            name: 'SL',
            minWidth: '20px',
            sortable: true,
            cell: (row, index) => index + 1  //RDT provides index by default
        },
        {
            name: 'Minimum',
            minWidth: '100px',
            sortable: true,
            selector: 'minimum'
        },
        {
            name: 'Maximum',
            minWidth: '100px',
            sortable: true,
            selector: 'maximum'
        },
        {
            name: 'Is Financial',
            minWidth: '100px',
            sortable: true,
            selector: (item) => {
                return item.isFinancial ? 'True' : 'False'
              } 
        },
        {
            name: 'Service Keyword',
            minWidth: '100px',
            sortable: true,
            selector: 'serviceKeyword'
        },
        {
            name: 'Rule Provider',
            minWidth: '100px',
            sortable: true,
            selector: 'ruleProvider'
        },
        {
            name: 'Created By',
            minWidth: '100px',
            sortable: true,
            selector: 'createdBy'
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Approve">
                        <CheckSquare size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 1, 'Do you want to approve?')}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Reject">
                        <XSquare size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 0, 'Do you want to reject?')}
                        />
                    </span>
                </>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom'>
                <CardTitle tag='h4'>Pending Service Logics</CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={pendingServiceList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default PendingServiceLogicList